import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View, Panel, PanelHeader, Header, Link, Div, Group, Avatar, Cell, Button, Search, List, CellButton, HeaderButton} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import { usersData } from './data.js'

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedUser: null,
		};
	}

	componentDidMount() {
    fetch(`https://monopoly-one.com/api/users.get?user_ids=48919,49105,726502,726505,49175`)
      .then(res => res.json())
      .then(json => this.setState({ users: json.data }));

  }

	renderUsers(users){
		let usersArr = [];
		for (let user of users) {
				usersArr.push(this.renderUser(user))
		}
		return usersArr
	}


	renderUser(user){
				return (
				 <Group>
					 <Cell
						 size="l"
						 before={<Avatar size={48} type="app" src={user.avatar} />}
					 >

						 <div style={{float:'right', fontSize:18,textAlign:'right',marginBottom:0}}>
							 Побед: {this.showWins(user.user_id)}
						 </div>

						 <div style={{fontWeight:'bold',fontSize:18}}>{user.nick}</div>
						 <img style={{width:75,height:30}} src={user.rank.img}/><br/>

					  {this.showVip(user.vip)}

					 </Cell>
				 </Group>
			 )
	}

	showVip(vip){
		if (vip==1){
			return(
				<span style={{fontWeight:'bold', color:'white', backgroundColor: '#f0a50c', padding: 4}}>VIP</span>
			)
		}
	}

	showWins(id){
		for (let userData of usersData) {
				if (userData.id == id){
					var result = userData.wins
				}
		}
		console.log(result)
		return result
	}


	render() {
		return (
			<View activePanel={this.state.activePanel}>
				<Panel id="home">

					<PanelHeader>
						Посиделки в монополию
					</PanelHeader>

					<Header>
						Игроки
					</Header>

					{
					 this.state && this.state.users &&
					 this.renderUsers(this.state.users)
					}
				</Panel>
			</View>
		);
	}
}

export default App;



